import React, { useLayoutEffect, useState } from "react";
import "./AudioCallGame.scss";
import SoundBtn from "../sound_button/SoundBtn";
import AnswerBtn from "../answer_btn/AnswerBtn";
import { Button, Switch } from "antd";
import { ArrowRightOutlined, CloseOutlined } from "@ant-design/icons";
import ImageAnswer from "../image_answer/ImageAnswer";
import FullscreenGame from "../../fullscreen_game/FullscreenGame";
import { useDispatch, useSelector } from "react-redux";
import { State } from "../../../redux/reducer/rootReducer";
import { sound } from "../../../redux/action/gameAction";
import StatisticModal from "../../statisticModal/StatisticModal";

interface Word {
  id: string;
  group: number;
  page: number;
  word: string;
  image: string;
  audio: string;
  audioMeaning: string;
  audioExample: string;
  textMeaning: string;
  textExample: string;
  transcription: string;
  wordTranslate: string;
  textMeaningTranslate: string;
  textExampleTranslate: string;
}
interface Props {
  words: Word[];
  setStart: any;
}

export default function AudioCallGame({ words, setStart }: Props) {
  const [audio] = useState(words.map((word) => word.audio));
  const [word] = useState(words.map((word) => word.wordTranslate));
  const [index, setIndex] = useState(0);
  const [arrFailBtnAnswer, setArrAnswer] = useState([...word]);
  const [isReplied, setIsReplied] = useState(false);
  const [stat, setStat] = useState<any>({successWords:[], failWords:[]});
  const [endGame, setEndGame] = useState(false);
  const dispatch = useDispatch();
  const soundState = useSelector((state: State) => state.savannah.sound);
  useLayoutEffect(() => {
    let arr = word
      .filter((w) => w !== word[index])
      .sort((a, b) => Math.random() - 0.5)
      .slice(0, 4);
    arr.push(word[index]);
    arr.sort((a, b) => Math.random() - 0.5);
    setArrAnswer(arr);
  }, [index]);
  const [streak, setStreak] = useState(0);
  const [streakStat, setStreakStat] = useState(0);

  const handleClick = () => {
    if (!isReplied) {
      setIsReplied(true);
      setStat({...stat, failWords: [...stat.failWords, words[index]]});
    } else if (index === words.length-1) {
      setEndGame(true);
      return;
    } 
    else {
      if (index < words.length) {
        setIndex(index + 1);
        setIsReplied(false);
      }
    }
  };

  return (
    <>
      {isReplied ? (
        <ImageAnswer
          audio={words[index].audio}
          image={words[index].image}
          word={words[index].word}
          audioExample={words[index].audioExample}
          audioMeaning={words[index].audioMeaning}
          transcription={words[index].transcription}
          textExample={words[index].textExample}
          textMeaning={words[index].textMeaning}
        />
      ) : (
        <SoundBtn audio={audio} index={index} />
      )}
      <AnswerBtn
        isReplied={isReplied}
        setIsReplied={setIsReplied}
        successWord={word[index]}
        words={arrFailBtnAnswer}
        sound={soundState}
        setStat={setStat}
        word={words[index]}
        stat={stat}
        index={index}
        streak={streak}
        setStreak={setStreak}
        streakStat={streakStat}
        setStreakStat={setStreakStat}
      />
      <Button
        onClick={handleClick}
        type="primary"
        danger={!isReplied}
        size="large"
      >
        {isReplied ? <ArrowRightOutlined /> : <CloseOutlined />}
      </Button>
      <FullscreenGame />
      <div className="sprint_sound_switch">
        <SvgSound className="sprint_sound_svg" soundState={soundState} />
          <Switch
            checked={soundState}
            onChange={() => dispatch(sound())
            }
          />
        </div>
     {endGame ? <StatisticModal streak={[streak, streakStat]} game={"Audiocall"} setStart={setStart} words={[stat.successWords, stat.failWords, words.length]} /> : ''}
    </>
  );
}

